@-webkit-keyframes fadeIn {
    0% {
        opacity: 0;
        -webkit-transform: translateY(60%);
                transform: translateY(60%);
    }

    50% {
        opacity: 0;
    }

    75% {
        opacity: 0.3;
    }

    100% {
        opacity: 1;
    }
}

@keyframes fadeIn {
    0% {
        opacity: 0;
        -webkit-transform: translateY(60%);
                transform: translateY(60%);
    }

