.activeButton {
    background-color: #4aa4fe;
    color: white;
    -webkit-animation: activeButton 0.4s ease-in-out;
            animation: activeButton 0.4s ease-in-out;
}

@-webkit-keyframes activeButton {
    from {
        -webkit-transform: scale(0.9);
                transform: scale(0.9);
    }

    to {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}

@keyframes activeButton {
    from {
        -webkit-transform: scale(0.9);
                transform: scale(0.9);
    }

    to {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}

